<script>
    import { initializeApp } from "firebase/app";   
    import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, updateProfile, signOut } from "firebase/auth";
    import { getFirestore, collection, addDoc, doc, setDoc } from "firebase/firestore"; 
    

    const firebaseConfig = {
        apiKey: "AIzaSyDo8hrI5KuCFH1cibXKhtz-st4eLMBJblA",
        authDomain: "revision-site-48465.firebaseapp.com",
        databaseURL: "https://revision-site-48465-default-rtdb.asia-southeast1.firebasedatabase.app",
        projectId: "revision-site-48465",
        storageBucket: "revision-site-48465.appspot.com",
        messagingSenderId: "72568118803",
        appId: "1:72568118803:web:7e880faa7845e6d04d69e6"
    };
    const app = initializeApp(firebaseConfig);

    let email, password, user, displayName;
    let errorMessage, perrorMessage;
    let userEmail;
    const db = getFirestore();
    const auth = getAuth();


    function singnUp() {
        createUserWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
            // Signed in 
            user = userCredential.user;
            // ...
            console.log(user)
            async function addUsertoCollection() {
                console.log(user.uid)
                await setDoc(doc(db, "users", user.uid), {
                Chemistry:[],
                Physics: [],
                IT: []
                });
                console.log(user.uid)
            }
            addUsertoCollection()
            })
        .catch((error) => {
            const errorCode = error.code;
            errorMessage = error.message;
        });
        updateProfile(auth.currentUser, {
            displayName: displayName, photoURL: ""
            }).then(() => {
            // Profile updated!
            // ...
            }).catch((error) => {
            // An error occurred
            // ...
        });
    }

    function signIn() {
        signInWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
            // Signed in 
            user = userCredential.user;
            // ...
            displayName = user.displayName
            userEmail = user.email
            console.log(userEmail)
            console.log(displayName)
            console.log(user)
        })
        .catch((error) => {
            const errorCode = error.code;
            errorMessage = error.message;
        });
        email, password = "";
    }
    async function signout() {
        signOut(auth).then(() => {
            console.log("signed out")
            }).catch((error) => {
            console.log(error)
        });
    }
</script>

<div class="mx-auto">
    <form action="">
        <br><br>
        <h2 class="text-lg text-red-500 font-bold">Sign up</h2>
        <input type="text" bind:value={displayName}>
        <input type="email" bind:value={email}>
        <input type="password" bind:value={password}>
        <input type="submit" on:click|preventDefault={singnUp}>

        <br>
        <h2 class="text-lg text-red-500 font-bold">Sign in</h2>
        <input type="email" bind:value={email}>
        <input type="password" bind:value={password}>
        <input type="submit" on:click|preventDefault={signIn}>
    </form>
    <p>Hey {displayName}, welcome back</p>
    <p>{perrorMessage}</p>
    <p>{errorMessage}</p>
    <p>{userEmail}</p>
    <button on:click={signout}>Sign-out</button>
</div>
