<script>

import { initializeApp } from "firebase/app";
import {
    getFirestore,
    collection,
    addDoc,
    query,
    updateDoc,
    limit,
    onSnapshot,
    setDoc,
    doc,
    serverTimestamp,
    where,
    QuerySnapshot,
    getDocs,
    arrayUnion,
    getDoc
} from 'firebase/firestore';
import { getAuth, onAuthStateChanged } from "firebase/auth";

const firebaseConfig = {
    apiKey: "AIzaSyDo8hrI5KuCFH1cibXKhtz-st4eLMBJblA",
    authDomain: "revision-site-48465.firebaseapp.com",
    databaseURL: "https://revision-site-48465-default-rtdb.asia-southeast1.firebasedatabase.app",
    projectId: "revision-site-48465",
    storageBucket: "revision-site-48465.appspot.com",
    messagingSenderId: "72568118803",
    appId: "1:72568118803:web:7e880faa7845e6d04d69e6"
};

const app = initializeApp(firebaseConfig);
const db = getFirestore();
const auth = getAuth();
let authstate, email, displayName, uid;
let usersCompletedTopics;
onAuthStateChanged(auth, (user) => {
    if (user) {
        uid = user.uid;
        authstate = uid;
        email = user.email;
        displayName = user.displayName;

        async function CompletedQuestions() {
            const docRef = doc(db, "users", uid);
            const docSnap = await getDoc(docRef);

            if (docSnap.exists()) {
                usersCompletedTopics = docSnap.data()
            } else {
            // doc.data() will be undefined in this case
            console.log("No such document!");
            }
        }
        CompletedQuestions()
        console.log(user)
    } else {
        authstate = "not logged in"
    }
});
$: console.log(usersCompletedTopics)

const subjectsCollection = {
    subjects: ["Physics", "Chemistry", "IT", "Biology"],
    Physics: ["Physical quantities and units", "Kinematics", "Dynamics", "Forces, density and pressure", "Work, energy and power", "Deformation of solids", "Waves", "Superposition", "Electricity", "D.C. circuits", "Particle physics"],
    Chemistry: ["Atomic structure", "Atoms, molecules and stoichiometry", "Chemical bonding", "States of matter", "Chemical energetics", "Electrochemistry", "Equilibria", "Reaction kinetics", "Periodic Table", "Group 2", "Group 17", "Nitrogen and sulfur", "Introduction to organic chemistry", "Hydrocarbons", "Halogen compounds" , "Hydroxy compounds", "Carbonyl compounds", "Carboxylic acids and derivatives", "Nitrogen compounds", "Polymerisation", "Organic synthesis"],
    IT: ["Data processing and information", "Hardware and software", "Monitoring and control", "Algorithms and flow charts", "eSecurity", "The digital divide", "Expert systems", "Spreadsheets", "Database and file concepts", "Sound and video editing"]
}
const selectedTopics = {
    Physics: [],
    Chemistry: [],
    Maths: [],
    IT: []
}
let numberOfQuestions = 0;
$: console.log(numberOfQuestions)
let selectedSubject = "placeholder";

let questionsId = []
let questionsObject = [];
async function getQuestions () {
    let q;
    if (selectedSubject == subjectsCollection.subjects[0]) {
            q = query(collection(db, selectedSubject), where("topic", "in", selectedTopics.Physics));
            console.log("physics")
    } else if (selectedSubject == subjectsCollection.subjects[1]){
            q = query(collection(db, selectedSubject), where("topic", "in", selectedTopics.Chemistry));
            console.log("chem")
    } else if (selectedSubject == subjectsCollection.subjects[2]) {
            q = query(collection(db, selectedSubject), where("topic", "in", selectedTopics.IT));
            console.log("IT")
    }
        const querySnapshot = await getDocs(q);
        console.log(querySnapshot)
        querySnapshot.forEach((doc) => {
            if (selectedSubject ==  "Physics") {
                if (!usersCompletedTopics.Physics.includes(doc.id)) {
                    questionsObject.push(doc.data())
                    questionsId.push(doc.id)
                    numberOfQuestions++
                }
            } else if (selectedSubject == "Chemistry") {
                if (!usersCompletedTopics.Chemistry.includes(doc.id)) {
                    questionsObject.push(doc.data())
                    questionsId.push(doc.id)
                    numberOfQuestions++
                }
            } else if (selectedSubject == "IT") {
                if (!usersCompletedTopics.IT.includes(doc.id)) {
                    questionsObject.push(doc.data())
                    questionsId.push(doc.id)
                    numberOfQuestions++
                }
            }
        })
        renderQuestions()
    }
let questionsIndex = 0;
let revealAnswer = false;
function incrementIndex() {
    if (questionsObject.length > questionsIndex + 1) {
        revealAnswer = false;
        questionsIndex++
        renderQuestions()
    }
}
function decrementIndex() {
    if ( questionsIndex >= 1) {
        revealAnswer = false
        questionsIndex--
        renderQuestions()
    }
}
function renderQuestions() {
    if (questionsObject[questionsIndex].fileType == "pdf") {
        document.getElementById("question-holder").innerHTML = `<iframe src="${questionsObject[questionsIndex].questionLink}#toolbar=0" class="w-full rounded-xl" height="650px"></iframe>`;
    } else if (questionsObject[questionsIndex].fileType == "PNG"){
        document.getElementById("question-holder").innerHTML = `<img src="${questionsObject[questionsIndex].questionLink}" alt="" class="w-full">`;
    }
    document.getElementById("answer-holder").innerHTML = `<img src="${questionsObject[questionsIndex].answerLink}" class="w-full rounded-xl">`;
    document.getElementById("details").innerHTML =`<p>Chapter: ${questionsObject[questionsIndex].topic}</p>`
    console.log(questionsId[questionsIndex])
}

async function answerCompleted() {
    const ref = doc(db, "users", uid);
    console.log("answered", uid, questionsId[questionsIndex], selectedSubject);
    if (selectedSubject == "Physics") {
        await updateDoc(ref, {
            Physics: arrayUnion(questionsId[questionsIndex])
        });
    } else if (selectedSubject == "Chemistry") {
        await updateDoc(ref, {
            Chemistry: arrayUnion(questionsId[questionsIndex])
        });
    } else if (selectedSubject == "IT") {
        await updateDoc(ref, {
            IT: arrayUnion(questionsId[questionsIndex])
        });
    }
}
console.log(usersCompletedTopics)
</script>

<div class="mx-auto rounded-sm">
    <h2 class="text-red-500 text-3xl text-center my-7">Select Subject</h2>
    <div class="flex justify-center">
        {#each subjectsCollection.subjects as subject}
            <div on:click={() => selectedSubject = subject} class="mx-6 px-12 py-4 rounded-md hover:bg-red-500 hover:text-white ring-2 ring-red-500 {selectedSubject == subject ? 'bg-red-500 text-white' : 'text-red-400'}">{subject}</div>
        {/each}
    </div>
    <div class="flex my-14">
        <div class="w-3/12 bg-red-500 flex flex-col text-white text-sm font-bold my-10">
            <h2 class="font-extrabold my-8 text-center text-2xl">Select Topics</h2>
            {#if selectedSubject == subjectsCollection.subjects[0]}
                {#each subjectsCollection.Physics as physicsTopic}
                <label class="m-1">
                    <input type="checkbox" bind:group={selectedTopics.Physics} value={physicsTopic}>
                    {physicsTopic}
                </label>                
                {/each}
            {:else if selectedSubject == subjectsCollection.subjects[1]}
                {#each subjectsCollection.Chemistry as ChemistryTopic}
                <label class="m-1">
                    <input type="checkbox" bind:group={selectedTopics.Chemistry} value={ChemistryTopic}>
                    {ChemistryTopic}
                </label>   
                {/each}
            {:else if selectedSubject == subjectsCollection.subjects[2]}
                {#each subjectsCollection.IT as ITTopic}
                <label class="m-1">
                    <input type="checkbox" bind:group={selectedTopics.IT} value={ITTopic}>
                    {ITTopic}
                </label>   
                {/each}
            {/if}

            <button on:click={getQuestions} class="my-6 mx-auto w-3/6 border-white border-4 py-3 rounded-full hover:bg-white hover:text-red-500 font-extrabold">Apply</button>
        </div>
        <div class="w-8/12 mx-auto ring-4 ring-red-500 rounded-lg mb-10">
            <div class="bg-red-500 py-2 flex justify-between text-white px-6">
                <button on:click={decrementIndex} class="border-none">
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 my-auto" viewBox="0 0 20 20" fill="currentColor">
                        <path fill-rule="evenodd" d="M15.707 15.707a1 1 0 01-1.414 0l-5-5a1 1 0 010-1.414l5-5a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 010 1.414zm-6 0a1 1 0 01-1.414 0l-5-5a1 1 0 010-1.414l5-5a1 1 0 011.414 1.414L5.414 10l4.293 4.293a1 1 0 010 1.414z" clip-rule="evenodd" />
                    </svg>
                </button>

                {#if revealAnswer == false}
                <button on:click={() => revealAnswer = true} class="border-white border-2 px-4 py-2 rounded-xl hover:text-red-500 hover:bg-white">Reveal answer</button>
                {:else if revealAnswer == true}
                <button on:click={() => revealAnswer = false} class="border-white border-2 px-4 py-2 rounded-xl hover:text-red-500 hover:bg-white">Show question</button>
                {/if}
                <div class="flex flex-col">
                    <p>Index: {questionsIndex + 1}/{numberOfQuestions}</p>
                    <div  id="details">

                    </div>
                </div>
                <button on:click={answerCompleted} class="border-white border-2 px-4 py-2 rounded-xl hover:text-red-500 hover:bg-white">answered</button>

                <button on:click={incrementIndex} class="border-none">
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 my-auto" viewBox="0 0 20 20" fill="currentColor">
                        <path fill-rule="evenodd" d="M10.293 15.707a1 1 0 010-1.414L14.586 10l-4.293-4.293a1 1 0 111.414-1.414l5 5a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                        <path fill-rule="evenodd" d="M4.293 15.707a1 1 0 010-1.414L8.586 10 4.293 5.707a1 1 0 011.414-1.414l5 5a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                    </svg>
                </button>
            </div>
                <div id="question-holder" class="rounded-lg {revealAnswer == false ? 'block' : 'hidden'}">
                </div>
                <div id="answer-holder" class="rounded-lg {revealAnswer == false ? 'hidden' : 'block'}">
                </div>
        </div>
    </div>
</div>

