<script>
import { initializeApp } from "firebase/app";
import { getFirestore, collection, addDoc} from "firebase/firestore"; 
import { getStorage, ref, uploadBytesResumable, getDownloadURL} from "firebase/storage";


const firebaseConfig = {
    apiKey: "AIzaSyDo8hrI5KuCFH1cibXKhtz-st4eLMBJblA",
    authDomain: "revision-site-48465.firebaseapp.com",
    databaseURL: "https://revision-site-48465-default-rtdb.asia-southeast1.firebasedatabase.app",
    projectId: "revision-site-48465",
    storageBucket: "revision-site-48465.appspot.com",
    messagingSenderId: "72568118803",
    appId: "1:72568118803:web:7e880faa7845e6d04d69e6"
};

const app = initializeApp(firebaseConfig);
const db = getFirestore();
const storage = getStorage();

/** @type {any} */
const metadata = {
    contentType: 'image/png'
};

const subjectsCollection = {
    subjects: ["Physics", "Chemistry", "IT", "Biology"],
    Physics: ["Physical quantities and units", "Kinematics", "Dynamics", "Forces, density and pressure", "Work, energy and power", "Deformation of solids", "Waves", "Superposition", "Electricity", "D.C. circuits", "Particle physics"],
    Chemistry: ["Atomic structure", "Atoms, molecules and stoichiometry", "Chemical bonding", "States of matter", "Chemical energetics", "Electrochemistry", "Equilibria", "Reaction kinetics", "Periodic Table", "Group 2", "Group 17", "Nitrogen and sulfur", "Introduction to organic chemistry", "Hydrocarbons", "Halogen compounds" , "Hydroxy compounds", "Carbonyl compounds", "Carboxylic acids and derivatives", "Nitrogen compounds", "Polymerisation", "Organic synthesis"],
    IT: ["Data processing and information", "Hardware and software", "Monitoring and control", "Algorithms and flow charts", "eSecurity", "The digital divide", "Expert systems", "Spreadsheets", "Database and file concepts", "Sound and video editing"]
}
const selectedTopics = {
    Physics: [],
    Chemistry: [],
    Maths: []
}
let selectedSubject;
let selectedTopic;
let questionLink;
let answerLink;
let questionOrigin;
let fileType;
function submitQuestion() {
	try {
    const docRef = addDoc(collection(db, selectedSubject), {
    subject: selectedSubject,
    topic: selectedTopic,
    questionLink: questionLink,
    answerLink: answerLink,
    questionOrigin: questionOrigin,
    fileType: fileType
    });
        console.log("Document written with ID: ", docRef.id);
        } catch (e) {
            console.error("Error adding document: ", e);
        }
    }

</script>

<main class="mx-auto rounded-sm w-8/12 bg-red-300 mt-20 p-5">
    <form action="" class="">

        <label for="subject">Subject:</label>
        <select bind:value={selectedSubject}>
            {#each subjectsCollection.subjects as subject}
            <option value={subject}>{subject}</option>
            {/each}
        </select>

        <label class="mt-5" for="{selectedSubject}-topics">{selectedSubject}-topics
            <select bind:value={selectedTopic}>
                {#if selectedSubject == subjectsCollection.subjects[0]}
                    {#each subjectsCollection.Physics as topic}
                        <option value={topic}>{topic}</option>
                    {/each}
                {:else if selectedSubject == subjectsCollection.subjects[1]}
                    {#each subjectsCollection.Chemistry as topic}
                    <option value={topic}>{topic}</option>
                    {/each}
                {:else if selectedSubject == subjectsCollection.subjects[2]}
                    {#each subjectsCollection.IT as topic}
                    <option value={topic}>{topic}</option>
                    {/each}
                {/if}
            </select>
        </label>

        <label class="mt-5" for="question-link">question-link:
            <br>
            <input type="text" bind:value={questionLink}>
        </label>

        <label class="mt-5" for="answer-link">answer-link:
            <br>
            <input type="text" bind:value={answerLink}>
        </label>

        <label class="mt-5" for="questionOrigin">Question-Origin:
            <br>
            <input type="text" bind:value={questionOrigin}>
        </label>

        <br>
        <label for="file-type">
            <select bind:value={fileType}>
                <option value="pdf">pdf</option>
                <option value="PNG">PNG</option>
            </select>
        </label>
    </form>
    <button class="mt-5 px-5 py-1" on:click={submitQuestion}>Submit</button>
</main>

